
@charset "UTF-8";
* {
  font-family: 微軟正黑體;
  padding: 0;
  margin: 0;
}
#app {
  background-color: #EAEAEA;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  min-height: 100vh;
}
.user-navbar {
  background-color: #333;
  border: 4px solid #fff;
  border-radius: 0;
}
.user-navbar .navbar-header .navbar-brand {
    color: #fff;
}
.user-navbar .navbar-toggle {
    border: none;
}
.user-navbar .navbar-toggle:hover .icon-bar {
      background-color: #FFF100;
}
.user-navbar .navbar-toggle:focus {
      background-color: transparent;
}
.user-navbar .navbar-toggle .icon-bar {
      background-color: #fff;
}
.user-navbar .navbar-toggle:hover {
      background-color: transparent;
}
.user-navbar .navbar-nav > li > a {
    padding-top: 15px;
    padding-bottom: 15px;
    font-size: 16px;
}
.user-navbar #navbar .nav li:hover a {
    color: #FFF100;
}
.user-navbar #navbar .nav li a {
    color: #fff;
}
.user-navbar #navbar .nav li.active a {
    background-color: #FFF100;
    color: #000;
}
@media (min-width: 768px) {
.user-navbar {
    margin-bottom: 0;
}
.user-navbar .navbar-brand {
      font-size: 20px;
}
.user-navbar .navbar-nav > li > a {
      font-size: 20px;
}
}



